import * as React from "react";
import { Form, FormGroup, Input, Label } from 'reactstrap';

export class SelectorPanel extends React.Component<any,any> {
    constructor(props: any) {
      super(props)
    }

  render() {
    const margin_style = {
      border: '#aaa',
      borderRadius: '5px',
      borderStyle: 'solid',
      borderWidth: '2px',
      padding: '15px'
    };
    return (
      <Form style={margin_style} className='text-center'>
        <FormGroup check inline={true}>
          <Label check style={{marginRight: '5px'}}>
            <Input type="checkbox" />
            <br/>
            Nodes
          </Label>
          <Label check style={{marginLeft: '5px'}}>
            <Input type="checkbox" />
            <br/>
            Edges
          </Label>
        </FormGroup>
      </Form>
    );
  }
}
